import Link from 'next/link'
import React from 'react'

function Navbar() {
  return (
    <div className='w-full h-[70px] bg-[#1F2233] text-[#C0CAF5] border-b border-[#2A2D3E] flex'>
       <Link href='/'> <div className='w-[50%] h-full border-r border-black flex justify-center items-center text-[#C0CAF5] hover:bg-[#7B5FFF] hover:text-white transition duration-300  cursor-pointer'>
            <a>Home</a>
        </div></Link>
        <Link href='/project'><div className='w-[50%] h-full border-r border-black flex justify-center items-center '>
            <a>Projects</a>
        </div></Link>
    </div>
  )
}

export default Navbar